<!--  -->
<template>
  <div v-show="visible" :class="isTeacher ? 'teacher' : 'student'" id="quiz-placeholder">
    <div class="close-bar">
      <span v-show="isTeacher || !forceJoin" @click="close" class="bjy-close">X</span>
    </div>
    <div class="placeholder"></div>
  </div>
</template>

<script>
const eventEmitter = BJY.eventEmitter;
const auth = BJY.auth;

export default {
  data() {
    return {
      visible: false,
      isTeacher: auth.isTeacher(),
      forceJoin: false
    };
  },
  methods: {
    open(data) {
      this.visible = true;
      this.$nextTick(() => {
        !this.isTeacher &&
          BJY.quizStudent.open({
            // 
            element: $("#quiz-placeholder .placeholder")[0],
            replace: false,
            //  
            forceJoin: data.forceJoin,
            // id
            quizId: data.quizId,
            //  
            solution: data.solution,

            // 
            onReady: function () {},
            // 
            onError: function () {},
          });
      });
    },
    close() {
      this.visible = false;
    },
    initTeacher() {
      BJY.quizTeacher.init({
        element: $("#quiz-placeholder .placeholder")[0],
        replace: false,
        // 
        onError: function (data) {
          alert(data.msg);
        },
        //  true false
        onBeforeStartQuiz: function () {
          return true;
        },
        //  true false
        onBeforePublishSolution: function () {
          return true;
        },
        onBeforeQuizDelete: function () {
            var promise = $.Deferred();
            var sure = confirm('');
            if (sure) {
              promise.resolve(true);
            } else {
              promise.resolve(false);
            }
            return promise
        },
      });
    },
    initStudent() {
      BJY.quizStudent.init();
    },
  },
  created() {
    eventEmitter
      .on(
        // 
        eventEmitter.QUIZ_START,
        (e, data) => {
          console.log(data);
          this.forceJoin = data.forceJoin;
          this.open(data);
        }
      )
      // 
      .on(eventEmitter.QUIZ_END, (e, data) => {
        !this.isTeacher && this.close();
      })
      .on(eventEmitter.QUIZ_CLOSE, (e, data) => {
        if (!data.force && $.isEmptyObject(data.solution)) {
            alert('');
            return;
        }
        this.close();
      })
      .on(
        // 
        eventEmitter.QUIZ_SOLUTION,
        (e, data) => {
          this.forceJoin = false;
          this.open(data);
        }
      )
      .on(eventEmitter.QUIZ_SUBMIT, (e, data) => {
        console.log(data);
        this.close();
      })
      .on("toggle_quiz_dialog", (e, data) => {
        this.visible ? this.close() : this.open();
      });
    eventEmitter.trigger(eventEmitter.QUIZ_REQ);
  },
  mounted() {
    this.isTeacher ? this.initTeacher() : this.initStudent();
  },
  beforeDestroy() {},
};
</script>

<style lang="scss" scoped>
#quiz-placeholder {
  position: absolute;
  z-index: 10;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
  background: #fff;

  &.student {
    width: 600px;
    height: 80%;

    .close-bar {
      position: absolute;
      width: 100%;
      top: 0;
    }
    .placeholder {
      top: 30px;
      bottom: 0;
      position: absolute;
      width: 100%;
    }
  }
  &.teacher {
    width: 800px;
  }

  .close-bar {
    background: #232323;
    height: 30px;
    text-align: right;

    .bjy-close {
      line-height: 30px;
      color: #fff;
      font-size: 20px;
      margin-right: 16px;
      cursor: pointer;
    }
  }
}
</style>
