<template>
  <div id="app">
    <Loading />
    <div v-if="loaded" ref="main" id="main">
      <HorseLamp />
      <div class="class-panel">
        <WhiteBorad />

        <div v-if="showStartBtn" class="start-btn" @click="handleClassStart"></div>
      </div>

      <div class="media-panel">
        <div class="wrap">
          <!--  -->
          <TeacherPlayer />
          <!--  -->
          <InteractionPanel />
        </div>
        <!--  -->
        <DocList v-if="isTeacher" />
      </div>

      <div class="footer">
        <SettingPanel />
        <FunctionPanel />
      </div>

      <!--  -->
      <ModalPanel />
    </div>
  </div>
</template>

<script>
// loading
import Loading from "./components/Loading.vue";
// 
import WhiteBorad from "./components/WhiteBoard.vue";
// 
import SettingPanel from "./components/SettingPanel";
// 
import InteractionPanel from "./components/InteractionPanel";
// 
import TeacherPlayer from "./components/TeacherPlayer";
// 
import FunctionPanel from "./components/FunctionPanel";
// 
import ModalPanel from "./components/ModalPanel";
// 
import HorseLamp from "./components/HorseLamp";
// 
import DocList from "./components/DocList";
// 
import language from "./language/main.js";

const eventEmitter = BJY.eventEmitter;
const auth = BJY.auth;
const store = BJY.store;
const config = BJY.config;

eventEmitter.OPEN_SETTING_DIALOG_TRIGGER = 'open_setting_dialog';

export default {
  name: "app",
  components: {
    Loading,
    WhiteBorad,
    SettingPanel,
    InteractionPanel,
    TeacherPlayer,
    FunctionPanel,
    HorseLamp,
    ModalPanel,
    DocList,
  },
  data() {
    return {
      loaded: false,
      webrtc: 1,
      isTeacher: false,
      showStartBtn: false,
    };
  },
  mounted() {
    /**
     * flashflash
     * flash
     */
    var url = "//live-cdn.baijiayun.com/js-sdk/1.15.0/player/extension/flash.swf";
    var html = BJY.createSWF("temp", "", url);

    var swf = $(html);
    $("body").prepend(swf);
    swf.css({
      position: "absolute",
      top: 0,
      width: 398,
      height: 298,
      opacity: 0,
    });
    // flash
    if (!this.supportFlash() && !this.webrtc) {
      this.$Dialog
        .show({
          title: "",
          content: `Flash<a class="opt-button"
            href="http://www.adobe.com/go/getflashplayer">flash</a>`,
          confirmText: "",
        })
        .then(() => {
          console.log("flash");
        })
        .catch((e) => {
          console.log(e);
        });
    }
    // webrtc 
    if (this.webrtc) {
      this.$Dialog
        .show({
          title: "",
          content: ``,
          confirmText: "",
        })
        .then(() => {
          console.log("");
        })
        .catch((e) => {
          console.log(e);
        });
    }
  },
  created() {
    // 
    let $body = $("body");

    store.watch(
      "class.started",
      (started) => {
        this.showStartBtn = auth.isTeacher() && !started;
      },
      true
    );

    eventEmitter
      .one(eventEmitter.CLASSROOM_CONNECT_TRIGGER, (event, data) => {
        // 
        var extension = BJY.getExtension();
        // webrtcrealtimecat.jsundefinedRTCat
        extension.init().then(() => {
          eventEmitter.trigger(eventEmitter.CLASSROOM_CONNECT_TRIGGER, data);
        });
        return false;
      })
      .on(eventEmitter.CLASSROOM_CONNECT_FAIL, () => {
        alert(language.ROOM_SERVER_CONNECT_FAIL);
      })
      .on(eventEmitter.LOGIN_CONFLICT, () => {
        alert(language.LOGIN_CONFLICT);
      })
      // 
      // 
      .one(eventEmitter.VIEW_RENDER_TRIGGER, () => {
        // VIEW_RENDER_TRIGGER
        this.loaded = true;
        this.isTeacher = auth.isTeacher();
        eventEmitter.trigger(eventEmitter.DOC_ALL_REQ);

        if (auth.isTeacher() || auth.isAssistant()) {
          store.set("class.speakState", config.SPEAK_STATE_FREE);
        } else {
          store.set("class.speakState", config.SPEAK_STATE_LIMIT);
        }

        if (auth.isPresenter() && store.get("class.started")) {
          setTimeout(() => {
            var player = BJY.Player.instances[BJY.store.get("user.id")];
            BJY.userPublish.setDevice(player, !player.videoOn, !player.audioOn);
          }, 1000);
        }

        if (auth.isStudent()) {
          $body.addClass("student");
        }

        // webRTC
        // webrtcindex.htmljs
        if (auth.isWebRTC()) {
          BJY.ScreenShareExtensionWebrtc.init();
        }
      })
      .one(eventEmitter.LOADING_PANEL_END, () => {
        // ui
        $("#main").css("opacity", 1);
      })
      .on(eventEmitter.CLASS_END, () => {
        // 
        eventEmitter.trigger(eventEmitter.MEDIA_SWITCH_TRIGGER, {
          audioOn: false,
          videoOn: false,
        });
      })
      .on(eventEmitter.CLASS_START, () => {
        // 
        if (auth.isPresenter()) {
          setTimeout(() => {
            var player = BJY.Player.instances[BJY.store.get("user.id")];
            BJY.userPublish.setDevice(player, !player.videoOn, !player.audioOn);
          }, 1000);
        }
      })
      .on(eventEmitter.CLASSROOM_CONNECT_SUCCESS, (event, data) => {
        // data.reconnect 
      })
      // 
      .on(eventEmitter.COMMAND_RECEIVE, (event, data) => {
        if (data.command && data.command.command_type === 'logout' && data.toId === store.get('user.id')) {
          alert('');
          location.reload();
        }
      });

    // 
    this.init();
  },
  methods: {
    init() {
      console.log(
        "******************************************************************************"
      );
      console.log("*    BJY JS SDK -  - " + BJY.version);
      console.log("*   API https://www.baijiayun.com/js-sdk/doc/#/README");
      console.log("*   https://www.baijiayun.com/js-sdk/doc/#/change-log");
      console.log(
        "*   https://www.baijiayun.com/js-sdk/doc/#/live?id=%e5%b8%b8%e8%a7%81%e9%97%ae%e9%a2%98"
      );
      console.log("*   webrtcwebrtc: 0");
      console.log("*   signhttps://dev.baijiayun.com/wiki/detail/1");
      console.log(
        "******************************************************************************"
      );
      // demo-
      var options = {
        prefixName: "tiansujing",
        env: "production",
        room_id: "19112041735674",
        user_number: "0",
        user_avatar: "//img.baijiayun.com/0bjcloud/live/avatar/v2/teacher.png",
        user_name: "",
        user_role: 0,
        sign: "731f5299af5179f99a17746f7c1bd20e",
        webrtc: 1,
      };

      if (location.href.includes("teacher=1")) {
        options = Object.assign(options, {
          user_name: "",
          user_role: 1,
          user_number: "1",
          sign: "53df87fc8d9bcf018269f0c6328a71cf",
        });
      }

      // debug 
      // webrtcurl &webrtc=0
      if (location.href.includes("debug=1")) {
        var url = prompt("");
        options = Object.assign(options, this.urlParser(url));
        console.log(options);
      }

      var classOption = {
        // 
        id: options.room_id,
        // 
        speakState: BJY.config.SPEAK_STATE_LIMIT,
      };
      if (Number(options.webrtc)) {
        classOption.webrtcType = Number(options.webrtc);
      }
      this.webrtc = classOption.webrtcType;

      BJY.init({
        env: options.env,
        privateDomainPrefix: options.prefixName,
        class: classOption,
        user: {
          number: options.user_number,
          avatar: decodeURIComponent(options.user_avatar),
          name: decodeURIComponent(options.user_name),
          type: Number(options.user_role),
        },
        teacher: {
          number: options.user_number,
          avatar: decodeURIComponent(options.user_avatar),
          name: decodeURIComponent(options.user_name),
          type: Number(options.user_role),
        },
        sign: options.sign,
      });
    },
    // url
    urlParser(url) {
      var params = {};
      if (url) {
        try {
          var queryStr = url.split("?")[1];
          var paramList = queryStr.split("&");
          paramList.forEach((item) => {
            var pair = item.split("=");
            params[pair[0]] = pair[1];
          });
          // 
          params.env = this.getEnv(url);
          // 
          params.prefixName = url.split(".")[0].split("//")[1];
        } catch (e) {
          alert("url");
        }
      }
      return params;
    },
    getEnv(url) {
      // 
      let hostName = url.match(/\/\/(.*)\./)[1];
      if (
        /test/.test(hostName) ||
        /localhost/.test(hostName) ||
        /\d{1,3}\.\d{1,3}\.\d{1,3}/.test(hostName)
      ) {
        return "test";
      }
      if (/beta/.test(hostName)) {
        return "beta";
      }
      if (/www/.test(hostName)) {
        return "";
      }
      return "";
    },
    supportFlash() {
      var swf;
      var plugins = navigator.plugins;

      if (plugins && plugins.length > 0) {
        swf = plugins["Shockwave Flash"];
      } else if (document.all) {
        // eslint-disable-next-line no-undef
        swf = new ActiveXObject("ShockwaveFlash.ShockwaveFlash");
      }
      return !!swf;
    },
    handleClassStart() {
      eventEmitter.trigger(eventEmitter.CLASS_START_TRIGGER);
    },
  },
};
</script>

