<template>
  <div id="roll-call-dialog"></div>
</template>

<script>
import language from "../../language/main";

let auth = BJY.auth;
let eventEmitter = BJY.eventEmitter;

export default {
  components: {},
  data() {
    return {
      language: language,
      status: 0,
      isStudent: auth.isStudent(),
      applyTimer: null,
      timeLeft: "",
      result: "",
    };
  },
  computed: {},
  watch: {},
  methods: {},
  created() {
    eventEmitter.on(
      eventEmitter.ROLL_CALL, // 
      (e, data) => {
        this.timeLeft = data.duration;
        this.$Dialog
          .show({
            title: "",
            content: `<span id="roll-call-time-left">${data.duration}</span>`,
            confirmText: "",
          })
          .then(() => {
            eventEmitter.trigger(eventEmitter.ROLL_CALL_RES);
          })
          .catch((e) => {
            console.log(e);
          });

        this.applyTimer = setInterval(() => {
          if (this.timeLeft === 0) {
            clearInterval(this.applyTimer);
            this.$Dialog.hide();
          }
          $("#roll-call-time-left").text(this.timeLeft);
          this.timeLeft--;
        }, 1000);
      }
    );
  },
  mounted() {},
  beforeDestroy() {},
};
</script>

